:root {
   --page-num: 0;
   --items-per-page: 5;
}

.container {
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
}

.control {
   background: radial-gradient(ellipse at bottom, #254a74, #030e1b);
   z-index: 1;
   font-size: 3vw;
   padding: 0 0.1vw 0.4vw 0.1vw;
   border-radius: 1em;
   color: #3f648f;
   cursor: pointer;
   height: 5vw;
   opacity: 0.9;
}

.control:disabled {
   opacity: 0.7;
   cursor: default;
}

.control:hover:enabled {
   color: #57779c;
   opacity: 1;
}

.control-left {
   padding-right: 0.5vw;
   margin-right: 0.5vw;
   border-bottom-left-radius: 0;
   border-top-left-radius: 0;
}

.control-right {
   padding-left: 0.5vw;
   margin-left: 0.5vw;
   border-bottom-right-radius: 0;
   border-top-right-radius: 0;
}

.slider {
   display: flex;
   flex-grow: 1;
   width: 0;
   transform: translateX(calc(var(--page-num) * -100%));
   transition: transform 400ms ease;
}

.slider .item {
   display: flex;
   justify-content: center;
   flex: 0 0 calc(100% / var(--items-per-page));
   max-width: calc(100% / var(--items-per-page));
}

.slider .item .poster {
   border-radius: 0.3em;
   background: radial-gradient(ellipse at bottom, #0d1e31, #010a14);
   box-shadow: 0 0 7px 0 black;
   aspect-ratio: 2 / 3;
   display: flex;
   flex-flow: column;
   align-items: center;
   justify-content: center;
   flex-grow: 1;
   margin: 0 min(5%, 0.4em);
   min-width: 0;
   overflow: hidden;
}

.slider .item .poster h1 {
   text-align: center;
   font-size: min(1.8vw, 2em);
   color: #283b50;
   margin: 0 5%;
}

.slider .item .poster img {
   aspect-ratio: 2 / 3; /* is necessary? */
   height: 100%;
   object-fit: fill;
   border-radius: inherit;
}

.slider .item .poster svg {
   color: #172535;
   width: 60%;
   height: 45%;
}